import React, { useState, useEffect, useRef } from 'react';
import { Link } from 'react-router-dom';
import { Radio, Icon, Checkbox, Button, Row, Col, Tag, Pagination, Modal, Select, message, Tooltip, Popconfirm } from 'antd';
import Axios from '@fuxi/axios-pro';
import * as API from '@/api';
import FilterSarchBar from '@/pages/FilterSearchBar'
import DataSetTagsModal from '@/pages/DataSetTagsModal';
import download from '@/util/download';

import style from './style.module.less';

const CancelToken = Axios.CancelToken;

const ORDER_METHOD = [
  '按时间/倒序',
  '按时间/正序',
  '按asset名称/倒序',
  '按asset名称/正序',
  '按project名称/倒序',
  '按project名称/正序',
]

enum OrderType {
  TimeDesc = 0,
  TimeAsc,
  AssetDesc,
  AssetAsc,
  ProjectDesc,
  ProjectAsc
}

const DataSet: React.FC<{isAdmin: boolean}> = () => {
  const [checkedList, setCheckedList] = useState<string[]>([]);
  const [currentPageList, setCurrentPageList] = useState([]);
  const [total, setTotal] = useState(0);
  const [page, setPage] = useState(1);
  const [num, setNum] = useState(12);
  const [viewMethod, setViewMethod] = useState('grid');
  const [showImg, setShowImg] = useState(false);
  const [selectedImg, setSelectedImg] = useState('');
  const [showEditTagModal, setShowEditTagModal] = useState(false);
  const [isMutipleTag, setIsMutipleTag] = useState(false);
  const [editTags, setEditTags] = useState([]);
  const [editTagAssetName, setEditTagAssetName] = useState('');
  const [filterParams, setFilterParams] = useState({});
  const [orderType, setOrderType] = useState(0);
  // const [selectedImg, setSelectedImg] = useState('');

  const allAssetNameRef = useRef([]);
  const cancelRef = useRef(null);
  const isFetchingListRef = useRef(false);

  useEffect(() => {
    fetchList();
  }, [page, num, filterParams, orderType])

  useEffect(() => {
    setCheckedList([]);
  }, [filterParams])

  useEffect(() => {
    const lastPage = Math.ceil(total / num);
    if (!lastPage) return;
    if (page > lastPage) setPage(lastPage);
  }, [total])

  function fetchAssetList(isFetchAll = false) {
    const sortParams = {} as {_direction: string; _sort: string};
    const ascItems = [OrderType.AssetAsc, OrderType.ProjectAsc, OrderType.TimeAsc];
    ascItems.includes(orderType) ? (sortParams._direction = 'asc') : (sortParams._direction = 'desc')

    switch(orderType) {
      case OrderType.AssetAsc:
      case OrderType.AssetDesc:
        sortParams._sort = 'assetname'
        break;
      case OrderType.ProjectAsc:
      case OrderType.ProjectDesc:
        sortParams._sort = 'projectname'
        break;
      case OrderType.TimeDesc:
      case OrderType.TimeAsc:
        sortParams._sort = 'createtime'
        break;
    }

    const params = {
      _page: page,
      _num: isFetchAll ? -1 : num,
      ...filterParams,
      ...sortParams
    }

    const cancelToken = new CancelToken(cancel => cancelRef.current = cancel);

    return API.assets.fetchList(params, cancelToken);
  }

  async function fetchList() {
    if ( isFetchingListRef.current === true ) cancelRef.current && cancelRef.current();

    try {
      isFetchingListRef.current = true;
      const { data } = await fetchAssetList()
      isFetchingListRef.current = false;

      if (!data) return;
      setCurrentPageList(data.items);
      setTotal(data.total);
    } catch(e) {
      console.log('cancel request');
    }
    
  }


  async function handleDownLoadAsset(assetname) {
    const result = await API.util.downloadAsset({assetname});

    download(result);
  }

  function getIcons(item) {
    function handlePreview() {
      setShowImg(true);
      setSelectedImg(item.preview);
    }

    function removeCheck() {
      setCheckedList(checkedList.filter(checkedAssetname => item.assetname !== checkedAssetname))
    }

    async function handleDelete() {
      const { data } = await API.assets.deleteAsset(item.assetname);
      removeCheck();
      message.success('删除成功');
      fetchList();
    }

    return (
      <div onClick={e => e.stopPropagation()}>
        <Icon onClick={handlePreview} type="eye" style={{marginRight: 5, cursor: 'pointer'}} />
        <Link to={`/dataset/${item.assetname}`} style={{color: '#000'}}>
          <Icon type="fullscreen" style={{marginRight: 5, cursor: 'pointer'}} />
        </Link>
        <Icon type="download"  onClick={() => handleDownLoadAsset(item.assetname)} style={{marginRight: 5, cursor: 'pointer'}}  />
        <Popconfirm
          title={<p style={{ marginBottom: 0 }}>{`你确定要删除${item.assetname}吗?`}</p>}
          onConfirm={handleDelete}
          okText="确定"
          cancelText="取消"
          icon={<Icon type="close-circle" className={style["confirm-icon"]} theme="filled" />}
        >
          <Icon type="delete"/>
        </Popconfirm>
      </div>
    )
  }

  function handleEditTag(isMutiple, assetname='', editTags = []) {
    setShowEditTagModal(true);
    setIsMutipleTag(isMutiple);
    setEditTags(editTags);
    if (assetname) setEditTagAssetName(assetname);
  }

  function handleCheck(assetname) {
    if (checkedList.includes(assetname)) {
      setCheckedList(checkedList.filter(item => item !== assetname));
    } else {
      checkedList.push(assetname);
      setCheckedList([...checkedList]);
    }
  }

  function getTags(tags, maxLength, maxItem) {
    const showingTags = tags.slice(0, maxItem);

    return (
      <>

        {
          showingTags.map((tag, index) => {
            const isOverflow =  tag.length > maxLength
            const visible = isOverflow ? {} : {visible: false}

            return (
              <Tooltip title={tag} {...visible}  key={tag}>
                <Tag>{isOverflow ? `${tag.substring(0, maxLength)}...` : tag}</Tag>
              </Tooltip>
            )
          })
           
        }

        {
          tags.length > maxItem && (
            <Tooltip overlay={tags.join('，')}>
              <Tag>...</Tag>
            </Tooltip>
          )
        }

      </>
    ) 
  }

  function getGridCard() {
    
    return (
      <ul className="card-wrapper" style={{flexWrap: 'wrap', display: viewMethod === 'grid' ? 'flex' : 'none'}}>
        {
          currentPageList.map((item) => {
            const {assetname, projectname, preview, tags} = item;
            const isChecked = checkedList.includes(assetname);

            return (
              <li key={assetname} style={{flex: '0 1 24%', width: '24%', marginBottom: 18, marginRight: '1.3%'}}>
                <div className={isChecked ? style.checked : ''} style={{position: 'relative', boxShadow: '0 2px 8px rgba(0, 0, 0, 0.15)', background: '#ccc'}}>
                  {isChecked && <Icon type="check" style={{position: 'absolute', color: '#fff', fontSize: 16, left: 3, top: 3, zIndex: 2}}/>}
                  <div style={{position: 'relative', overflow: 'hidden', height: 250, cursor: 'pointer'}} onClick={() => handleCheck(assetname)}>
                    <img height='100%' style={{marginLeft: '50%', transform: 'translateX(-50%)'}} src={item.preview} onError={(e)=>{ e.target.src="./static/images/notfound.svg"}} alt=""/>
                    <div style={{ position: 'absolute', top: 8, right: 8 }}>
                      {getIcons(item)}
                    </div>

                    <div style={{position: 'absolute', bottom: 8, left: 8}}>
                      <div style={{ marginBottom: 10, fontSize: 16, fontWeight: 700}} >
                        {assetname}
                      </div>

                      <div>
                        {projectname}
                      </div>

                    </div>
                  </div>

                  <div style={{padding: 8, background: '#fff'}}>
                    {
                      getTags(tags, 3, 3)
                    }
                    <Icon onClick={() => handleEditTag(false, assetname, tags)} type="setting" style={{cursor: 'pointer'}}/>
                  </div>
                </div>
              </li>
            )
          })
        }
      </ul>
    )
  }

  function getColumnCard() {
    return (
      <ul style={{ display: viewMethod === 'grid' ? 'none' : 'block' }}>
        {
          currentPageList.map(item => {
            const {assetname, projectname, preview, tags, createtime} = item;
            const isChecked = checkedList.includes(assetname);

            return (
              <li onClick={() => handleCheck(assetname)} key={item.assetname} className={isChecked ? style.checked : ''} style={{width: '100%', height: 120, padding: 14, position: 'relative', cursor: 'pointer', marginTop: 20, boxShadow: '0 2px 8px rgba(0, 0, 0, 0.15)', background: '#fff'}}>
                <div style={{position: 'absolute', right: 14, top: 14}}>
                  {getIcons(item)}
                </div>
                {isChecked && <Icon type="check" style={{position: 'absolute', color: '#fff', fontSize: 16, left: 3, top: 3, zIndex: 2}}/>}
                <div style={{display: "flex", justifyContent: 'flex-start'}}>
                  <div style={{position: 'relative', height: 92, width: 92, overflow: 'hidden', background: '#ccc'}}>
                    <img src={'./static/images/notfound.svg'} onError={(e)=>{ e.target.src="./static/images/notfound.svg"}} height="92" alt="" style={{marginLeft: '50%', transform: 'translateX(-50%)'}}/>
                  </div>
                  <div style={{display: "flex", flexDirection: 'column', justifyContent: 'space-between', marginLeft: 14}}>
                    <span style={{fontSize: 16, fontWeight: 700}}>{assetname}</span>
                    <span>{projectname},{createtime}</span>
                    <span>
                      {
                        getTags(tags, 6, 9)
                      }
                      <Icon
                        onClick={(e) => {
                          e.stopPropagation();
                          handleEditTag(false, assetname, tags)
                        }}
                        type="setting"
                        style={{cursor: 'pointer'}}
                      />
                    </span>
                  </div>
                </div>
              </li>
            )
          })
        }
      </ul>
    )
  }

  function handleCheckCurrentPage(e) {
    const checked = e.target.checked;
    const concatList = checkedList.concat(currentPageList.map(item => item.assetname));
    if (checked) {
      setCheckedList([...new Set(concatList)])
    } else {
      const currentPageAssetNames = currentPageList.map(item => item.assetname);
      setCheckedList(checkedList.filter(assetname => !currentPageAssetNames.includes(assetname)));
    }
  }

  function handleCheckAll(e) {
    const { checked } = e.target;

    if (!checked) return setCheckedList([]);

    async function fetchAll() {
      const { data } = await fetchAssetList();
      const { items } = data;

      allAssetNameRef.current = items.map(item => item.assetname);
      setCheckedList( allAssetNameRef.current);
    }

    if (!allAssetNameRef.current.length) return fetchAll();
    return setCheckedList( allAssetNameRef.current);
  }

  function isCheckAll() {
    if (!allAssetNameRef.current.length) return false;
    return checkedList.length === allAssetNameRef.current.length;
  }

  function isCheckAllIndeterminate() {
    return checkedList.length !== 0 && !isCheckAll();
  }

  function isCheckCurrentPageIndeterminate() {
    return currentPageList.some(item => checkedList.includes(item.assetname)) && !isCheckCurrentPage() ;
  }

  function isCheckCurrentPage() {
    return currentPageList.length !== 0 && currentPageList.every(item => checkedList.includes(item.assetname));
  }

  function handleSearch(params) {
    setFilterParams(params);
  }

  return (
    <>
      <FilterSarchBar
        wrapperStyle={{paddingBottom: 40}}
        isHomePage={false}
        handleFilter={handleSearch}
      />
      <div style={{display: 'flex', justifyContent: 'space-between'}}>
        <div>
          <Radio.Group value={viewMethod} onChange={(e) => setViewMethod(e.target.value)}>
            <Radio.Button value="grid"><Icon type="appstore" /></Radio.Button>
            <Radio.Button value="column"><Icon type="menu" /></Radio.Button>
          </Radio.Group>
          
          <Select
            defaultValue={ORDER_METHOD[0]}
            style={{marginLeft: 10, width: 200}}
            onChange={e => setOrderType(e)}
          >

            {
              ORDER_METHOD.map((name, index) => (
                <Select.Option key={name} value={index}>
                  {name}
                </Select.Option>
              ))
            }

          </Select>
        </div>
        <div>
          <span style={{marginLeft: 10}}>已选{checkedList.length}项</span>
          <span style={{marginLeft: 10}}>
            <Checkbox onChange={handleCheckAll} indeterminate={isCheckAllIndeterminate()} checked={isCheckAll()}>全选</Checkbox>
          </span>
          <span style={{marginLeft: 10}}>
            <Checkbox onChange={handleCheckCurrentPage} indeterminate={isCheckCurrentPageIndeterminate()} checked={isCheckCurrentPage()} >选中当前页</Checkbox>
          </span>
          <span style={{marginLeft: 10}}>
            <Tooltip title={`请选择多个项（≥2）进行批量添加标签`} {...(checkedList.length >= 2 ? { visible: false } : {})}>
              <Button onClick={() => handleEditTag(true)} disabled={checkedList.length < 2}>批量添加标签</Button>
            </Tooltip>
          </span>
        </div>
      </div>

        <div style={{marginTop: 20}}>
          {
            <>
              {getGridCard()}
              {getColumnCard()}
            </>
          }
        </div>

      <Pagination
        showQuickJumper
        showSizeChanger
        defaultCurrent={1}
        current={page}
        defaultPageSize={12}
        pageSizeOptions={['12', '24', '36', '48']}
        total={total}
        onChange={page => setPage(page) }
        onShowSizeChange={(page, size) => {
          setPage(page);
          setNum(size);
        }}
        style={{float: "right", marginTop: 30}}
      />
      <div style={{clear: "right"}} />
      <Modal onCancel={() => setShowImg(false)} className="img-modal-wrapper" style={{paddingTop: 30, paddingBottom: 30}} visible={showImg} width={680} footer={null}>
        <img src={selectedImg} width="100%" alt=""/>
      </Modal>

      {
        showEditTagModal && (
          <DataSetTagsModal
            defaultTags={editTags}
            isMutipleTag={isMutipleTag}
            onClose={() => setShowEditTagModal(false)}
            onSuccess={fetchList}
            checkedList={checkedList}
            editTagAssetName={editTagAssetName}
          />
        )
      }
    </>
  )
}

export default DataSet;