import React, { useState } from 'react';
import { Modal, Input, Upload, Button, Icon, InputNumber } from 'antd';
import Form, { FormComponentProps } from 'antd/lib/form';

const COMPONENT_FIELDS = {
  body: [
    {
      name: 'model',
      isRequired: true,
    },
    {
      name: 'format',
      isRequired: true
    },
    {
      name: 'comment',
      isRequired: false,
      defaultValue: ''
    },
    {
      name: 'fps',
      isRequired: true,
      isInt: true
    },
    {
      name: 'framenum',
      isRequired: false,
      isInt: true
    },
    {
      name: 'startframe',
      isRequired: false,
      defaultValue: 0,
      isInt: true
    },
    {
      name: 'endframe',
      isRequired: false,
      defaultValue: 0,
      isInt: true
    },
    {
      name: 'orderformat',
      isRequired: false
    },
    {
      name: 'coordinate',
      isRequired: false
    }
  ],
  face: [
    {
      name: 'model',
      isRequired: true,
    },
    {
      name: 'format',
      isRequired: true
    },
    {
      name: 'comment',
      isRequired: false,
      defaultValue: ''
    },
    {
      name: 'vertexnum',
      isRequired: false,
      isInt: true
    },
    {
      name: 'facenum',
      isRequired: false,
      isInt: true
    },
    {
      name: 'expdim',
      isRequired: false,
      isInt: true
    },
    {
      name: 'iddim',
      isRequired: false,
      isInt: true
    },
  ],
  audio: [
    {
      name: 'name',
      isRequired: false,
    },
    {
      name: 'format',
      isRequired: true
    },
    {
      name: 'samplerate',
      isRequired: true,
      isInt: true
    }
  ],
  video: [
    {
      name: 'name',
      isRequired: false,
    },
    {
      name: 'format',
      isRequired: true
    }
  ],
  feature: [
    {
      name: 'name',
      isRequired: false,
    }
  ]
}

type Props = {
  type: string;
  isCreate: boolean;
  onEditUpload?;
  isReplaceLoading?: boolean
}

const UrlValue = ({value = ''}) => {

  return (
    <span style={{wordBreak: 'break-all'}}>{value}</span>
  )
}

const ComponentForm: React.FC<Props & FormComponentProps> = ({ form, type, isCreate, onEditUpload, isReplaceLoading }) => {
  const [fileList, setFileList] = useState([]);

  const { getFieldDecorator, setFieldsValue } = form;

  const formItemLayout = {
    labelCol: {
      xs: { span: 24 },
      sm: { span: 4 }
    },
    wrapperCol: {
      xs: { span: 24 },
      sm: { span: 20 }
    }
  };

  const uploadProps = {
    fileList,
    onRemove: () => {
      setFileList([]);
      setFieldsValue({ file: undefined });
    },
    beforeUpload: file => {
      setFileList([file]);
      setFieldsValue({ file });
      return false;
    }
  };

  const editUploadProps = {
    showUploadList: false,
    beforeUpload: file => {
      onEditUpload(file);
      return false;
    }
  }

  return (
    <Form>
      {
        COMPONENT_FIELDS[type].map(({name, isRequired, isInt, defaultValue}: {name: string; isRequired?: boolean; isInt: boolean; defaultValue: any}) => (
          <Form.Item {...formItemLayout} key={name} label={name}>
            {getFieldDecorator(name, {
              rules: [
                { required: isRequired, message: `${name}不能为空` }
              ],
              initialValue: defaultValue !== undefined ? defaultValue : ''
            })(
              isInt ? <InputNumber style={{background: isCreate ? '' : '#f0f3ff', width: '100%'}} /> : <Input style={{background: isCreate ? '' : '#f0f3ff'}}/>
            )}
          </Form.Item>
        ))
      }
      {
        isCreate ? (
          <Form.Item {...formItemLayout} label="file">
            {getFieldDecorator('file', {
              rules: [
                { required: true, message: `文件不能为空` }
              ]
            })(
              <Upload {...uploadProps}>
                <Button>
                  <Icon type="upload" /> 点击上传
                </Button>
              </Upload>
            )}
          </Form.Item>
        ) : (
          <Form.Item {...formItemLayout} label="url">
            {getFieldDecorator('url', {})(
              <UrlValue />
            )}
            <Upload {...editUploadProps}>
                <Button size="small" style={{marginLeft: 20}} loading={isReplaceLoading}>替换</Button>
              </Upload>
          </Form.Item>
        )
      }
    </Form>
  )
}

export default ComponentForm;