import React, { useState, useRef, useEffect } from 'react'
import { List, Row, Col, Progress, message } from 'antd';
import Axios from '@fuxi/axios-pro';

import * as API from '@/api'

const CancelToken = Axios.CancelToken;

interface Props {
  assetname: string,
  files: File[];
  infoFile: File;
  handleUploadSuccess(assetname: string): void;
  show: boolean;
}

interface metaDataProps {
  requestid: number;
  assetname: string;
  projectname: string;
  component_num: string;
  createtime: string;
}

const STATUS = {
  prepare: '请求上传中',
  uploading: '上传中',
  pause: '暂停中',
  final: '文件生成中',
  fail: '上传失败'
}

const UploadingList: React.FC<Props> = ({ assetname, files, infoFile, handleUploadSuccess, show }) => {
  const [metaData, setMetaData] = useState<metaDataProps>(null);
  const [uploadedSize, setUploadedSize] = useState(0);
  const [status, setStatus] = useState(STATUS.prepare);
  const [isPause, setIsPause] = useState(false);
  
  const totalSize = useRef(0);
  const metaDataRef = useRef(null);
  const uploadQueue = useRef([...files]);
  const cancelRef = useRef(null);
  const failedFileRef = useRef(null);
  const retryTimeRef = useRef(0);
  const finishRetryTimeRef = useRef(0);
  const isPauseRef = useRef(false);

  function handleFinishError() {
    sendComplete();
  }

  async function sendComplete() {
    setStatus(STATUS.final);
    try {
      const { data } = await API.upload.finish({ requestid: metaDataRef.current.requestid });
      handleUploadSuccess(assetname);
    } catch (e) {
      if (finishRetryTimeRef.current >= 2) {
        return message.error('生成文件时出错');
      }
      finishRetryTimeRef.current = finishRetryTimeRef.current + 1;
      handleFinishError()
    }

  }

  function onError() {
    if (isPauseRef.current) return;
    if (retryTimeRef.current >= 3) {
      return message.error(STATUS.fail);
      handleUploadSuccess(assetname);
    }

    retryTimeRef.current = retryTimeRef.current + 1;
    upload(failedFileRef.current, uploadedSize);
  }

  function onSuccess(currentSize, file) {
    retryTimeRef.current = 0;
    setUploadedSize(currentSize + file.size);
    upload(uploadQueue.current.pop(), currentSize + file.size);
  }

  async function upload(file, currentSize = 0) {
    if (!file) return sendComplete();

    const { requestid, assetname } = metaDataRef.current;
    const fd = new FormData();

    fd.append('requestid', requestid);
    fd.append('assetname', assetname);
    fd.append('file', file);

    const cancelToken = new CancelToken( c => {cancelRef.current = c});

    try {
      const { data } = await API.util.upload(fd, cancelToken);
      onSuccess(currentSize, file)

    } catch(e) {

      failedFileRef.current = file;
      onError()
    }
  }

  async function fetchIdAndUpload() {
    const fd = new FormData();
    fd.append('file', infoFile);
    const { data } = await API.util.fetchMetaData(fd);
    // debugger
    setMetaData(data);
    metaDataRef.current = data;
    setStatus(STATUS.uploading);
    upload(uploadQueue.current.pop());
  }

  function getTotleFilesSize() {
    const size = files.reduce((size, currentFile) => {
      return size + currentFile.size;
    }, 0);
    totalSize.current = size;
  }

  function getMegabyteFormat(size) {
    return (size / (1024 * 1024)).toFixed(2);
  }
  
  function resume() {
    upload(failedFileRef.current, uploadedSize);
    setIsPause(false);
    isPauseRef.current = false
  }

  function pause() {
    if (status === STATUS.final) return message.error('文件生成中，无法暂停');
    setIsPause(true);
    isPauseRef.current = true;
    cancelRef.current && cancelRef.current();
  }

  function handlePauseOrResume() {
    isPause ? resume() : pause();
  }

  useEffect(() => {
    fetchIdAndUpload()
    getTotleFilesSize()
  }, [])

  if (!metaData) return null;
  if (!show) return null;

  return (
    <List.Item>
      <Row style={{ width: '100%', display: 'flex', alignItems: 'center' }}>
        <Col span={4}>
          <div>{assetname}</div>
          <div>{`${metaData.projectname}${metaData.projectname ? ',' : ''}${metaData.component_num} compoments`}</div>
        </Col>
        <Col span={8} style={{textAlign: 'center'}}>
          {metaData.createtime}
        </Col>
        <Col span={8}>
          <Progress
            percent={uploadedSize / totalSize.current * 100}
            showInfo={false}
            strokeColor="#1890ff"
          />
          <div style={{ display: 'flex', justifyContent: 'space-between'}}>
            <span>{`${status}...`}</span>
            <span>{`${getMegabyteFormat(uploadedSize)}MB/${getMegabyteFormat(totalSize.current)}MB`}</span>
          </div>
        </Col>
        <Col offset={2} span={2} style={{textAlign: 'right'}}>
          <span style={{ cursor: 'pointer', color: '#3E69FA' }} onClick={handlePauseOrResume}>{isPause ? '继续' : '暂停'}</span>
        </Col>
      </Row>
    </List.Item>
  )
}

export default UploadingList;