import { URLGroup } from '@fuxi/axios-pro';
import Axios from './common';

const prefix = URLGroup('');

export const util = {
  fetchMetaData(payload) {
    return Axios.post(prefix`/ids`, payload, { headers: { 'Content-Type': 'multipart/form-data' } })
  },

  upload(payload, cancelToken) {
    return Axios.post(prefix`/upload`, payload, {cancelToken, headers: { 'Content-Type': 'multipart/form-data' }});
  },

  downloadAsset(params) {
    return Axios.get(prefix`/download`, { params, responseType: 'arraybuffer'});
  }

};
