import { URLGroup } from '@fuxi/axios-pro';
import Axios from './common';

const prefix = URLGroup('/upload');

interface UploadListParams {
  createtime__lt? :string;
  assetname? :string;
  projectname? :string;
  _num? :number;
  _page? :number;
  _sort? :string;
  _direction? :string;
  _offset? :number;
  createtime__gt? :string;
}

export const upload = {
  uploadFile(payload: {file: Blob, requestid: string, assetname: string}) {
    return Axios.post(prefix``, payload, { headers: { 'Content-Type': 'multipart/form-data' } })
  },

  fetchUploadList(params: UploadListParams, cancelToken ) {
    // debugger
    return Axios.get(prefix`/files`, { params, cancelToken } );
  },

  finish(payload: {requestid: string}) {
    return Axios.post(prefix`/finish`, payload);
  }

};
