import { URLGroup } from '@fuxi/axios-pro';
import Axios from './common';

const prefix = URLGroup('/assets');

export const assets = {
  fetchList(payload, cancelToken = null) {
    return Axios.post(prefix``, payload, { cancelToken });
  },

  fetchAsset(assetname) {
    return Axios.get(prefix`/@${assetname}`);
  },

  fetchComponent(assetname, component_type, component_name) {
    return Axios.get(prefix`/@${assetname}/components/${component_type}s/@${component_name}`);
  },

  searchTags(params) {
    return Axios.get(prefix`/tags`, { params });
  },

  addTags(payload: {assetnames: string[], tags: string[]}) {
    return Axios.post(prefix`/tags`, payload);
  },

  addComponent(assetname, component_type, payload: FormData) {
    return Axios.post(prefix`/@${assetname}/components/@${component_type}s`, payload);
  },

  updateTags(assetname, payload: {tags: string[]}) {
    return Axios.put(prefix`/@${assetname}/tags`, payload);
  },

  updateAsset(assetname, payload) {
    return Axios.put(prefix`/@${assetname}`, payload);
  },

  updateComponent(assetname, component_type, component_name, payload) {
    return Axios.put(prefix`/@${assetname}/components/${component_type}s/@${component_name}`, payload);
  },

  updateComponentFile(assetname, component_type, component_name, payload: FormData) {
    return Axios.put(prefix`/@${assetname}/components/${component_type}s/files/@${component_name}`, payload);
  },

  deleteAsset(assetname) {
    return Axios.delete(prefix`/@${assetname}`);
  },

  deleteComponent(assetname, component_type, component_name) {
    return Axios.delete(prefix`/@${assetname}/components/${component_type}s/@${component_name}`);
  },

  downloadComponent(assetname, component_type, component_name) {
    return Axios.get(prefix`/@${assetname}/components/${component_type}s/@${component_name}/download`, {responseType: 'arraybuffer'});
  }

};
