import React, { useState, useEffect } from 'react';
import { Menu, Layout } from 'antd';
import { Link, withRouter, RouteComponentProps } from 'react-router-dom';
import * as API from '@/api'

import style from './style.module.less';

type Props = {
  setIsAdmin: React.Dispatch<React.SetStateAction<boolean>>;
}
const Header: React.FC<Props & RouteComponentProps> = (props) => {
  const [userName, setUserName] = useState('');
  const [isAdmin, setIsAdmin] = useState('');

  // debugger
  const menu = [
    {
      path: '/homepage',
      text: '首页'
    },
    {
      path: '/dataset',
      text: '数据集'
    },
    {
      path: '/upload',
      text: '上传管理',
      // hide: !isAdmin
    }
  ];

  function getRootPath() {
    const { pathname } = props.location;
    const index = pathname.indexOf('/', 1);
    return index === -1 ? pathname : pathname.substring(0, index);
  }

  function handleLogin() {
    const rbac = RbacURL || 'http://rbac.fuxi.netease.com:35615';
    location.href = `${rbac}/login?next=${location.href}`;
  }

  useEffect(() => {
    function getCookieValue(cookieName) {
      const result = document.cookie.match('(^|[^;]+)\\s*' + cookieName + '\\s*=\\s*([^;]+)');
      return result ? result.pop() : '';
    }

    async function fetchUserInfo() {
      const user = getCookieValue('RBAC_USER');
      if (!user) handleLogin();

      try {
        const { data: rulesData } = await API.user.fetchUserRules(user);
        const { data: userData } = await API.user.fetchUserName(user);
        const isAdmin = rulesData && rulesData.some(rule => rule.code === 'humandb.admin' && rule.project === 'humandb');
        const userName = userData.fullname;
        props.setIsAdmin(isAdmin);
        setIsAdmin(isAdmin);
        setUserName(userName);
      } catch(e) {
        console.log(e);
        handleLogin();
      }
    }

    fetchUserInfo()
  }, []);

  return (
    <Layout>
      <Layout.Header className={style['header-wrapper']} >
        <div className={style['header-inner']}>
          <Link to='/homepage' className={style['header-logo']}>HumanDB</Link>
            <Menu
              theme="dark"
              mode="horizontal"
              selectedKeys={[getRootPath()]}
              style={{ lineHeight: '64px' }}>
              {menu.filter(item => !item.hide).map((config, i) => (
                <Menu.Item key={config.path} style={{marginLeft: 40, background: 'transparent'}}>
                  <Link to={config.path}>{config.text}</Link>
                </Menu.Item>
              ))}
            </Menu>
            <div style={{flex: 1}} />
            <div style={{color: 'rgba(255, 255, 255, 0.65)', cursor: 'pointer'}}>{userName}</div>
        </div>
      </Layout.Header>
    </Layout>
  )
}

export default withRouter(Header);