import React from 'react';
import { BrowserRouter, Route, Switch, Redirect } from 'react-router-dom';

import Router from '@/pages/Router';

let routes: JSX.Element[] = [];

const configSorter = (a: RouteConfig, b: RouteConfig) => {
  let pa = a.priority || 0;
  let pb = b.priority || 0;
  return pa - pb;
};

function resolveRouteConfig(config: RouteConfig, prefix = '') {
  const path = prefix + (config.route || '');

  const commonProps = {
    key: path,
    path,
    exact: !!config.exact
  };
  const item = config.redirect ? (
    <Redirect {...commonProps} to={config.redirect} />
  ) : (
    <Route {...commonProps} component={config.component} />
  );

  routes.push(item);

  (config.children || []).sort(configSorter).forEach(childConfig => {
    resolveRouteConfig(childConfig, path);
  });

  return routes;
}

function importAll(r: ReturnType<typeof require.context>) {
  const allImportedModules: RouteConfig[] = r.keys().map(key => r(key).default);

  allImportedModules.sort(configSorter).forEach(config => {
    routes = routes.concat(resolveRouteConfig(config));
  });
}

importAll(require.context('./', false, /^((?!index).)*\.ts$/));

export default () => {

  return (
    <div>
      <BrowserRouter>
        <Switch>
          <Route path="/" component={Router} />
        </Switch>
      </BrowserRouter>
    </div>
  );
};
