function mock() {
  return Promise.resolve([{ name: 'task_one' }, { name: 'task_two' }]);
}

const service = { mock };

const state: TaskModelState = {
  tasks: []
};

type Action = {
  addTask: DvaAction<Task | Task[]>;
  sync: DvaAction<undefined>;
};

const reducers: DvaReducerMapObject<TaskModelState> = {
  addTask(state, { payload }: Action['addTask']) {
    return { ...state, tasks: state.tasks.concat(payload) };
  }
};

const effects: DvaEffectsMapObject = {
  *addsTask(_, { put, call }) {
    const data = yield call(service.mock);
    yield put({ type: 'addTask', payload: data });
  }
};

const task: DvaModel<TaskModelState, Action[keyof Action]> = {
  namespace: 'task',
  state,
  reducers,
  effects
};

export default task;
