import React, { useState, useEffect, useRef } from 'react';
import { Slider, Row, Col, Icon } from 'antd';

import style from './style.module.less'

type Props = {
  src: string;
  type: 'video' | 'audio';
}

const MediaPlayer: React.FC<Props> = ({ type, src }, ref) => {
  const [isPlaying, setIsPlaying] = useState(false);
  const [duration, setDuration] = useState(0);
  const [currentDuration, setCurrentDuration] = useState(0);
  const [waves, setWaves] = useState([]);

  const media = useRef(null);
  const analyserRef = useRef(null);
  const animationRef = useRef(null);

  function handlePause() {
    setIsPlaying(false);
    setWaves([]);
    animationRef.current && cancelAnimationFrame(animationRef.current);
  }

  function initMedia () {
    media.current.src = src;
    media.current.crossOrigin = "anonymous";
    media.current.preload = 'metadata';
    media.current.onloadedmetadata = () => {
      // debugger
      setDuration(Math.floor(media.current.duration));
    };
    media.current.ontimeupdate = (e) => {
      setCurrentDuration(Math.floor(media.current.currentTime));
    }
    media.current.onended = () => {
      handlePause();
      media.current.currentTime = 0;
    }

    if (type === 'audio') {
      const context = new AudioContext();
      const src = context.createMediaElementSource(media.current);
      const analyser = context.createAnalyser();
      analyserRef.current = analyser;
      src.connect(analyser);
      analyser.connect(context.destination);
  
      analyser.fftSize = 256;
    }
  }

  useEffect(() => {
    initMedia()
  }, [type]);

  useEffect(() => {
    handlePause();
  }, [type, src]);
  

  function renderSoundWave() {
    const waveArray = new Uint8Array(13);
    animationRef.current = requestAnimationFrame(renderSoundWave);

    analyserRef.current.getByteFrequencyData(waveArray);

    setWaves(Array.from(waveArray));
  }

  function handlePlayOrPause() {
    if (media.current.paused) {
      media.current.play();
      setIsPlaying(true);

      if (type === 'audio') {
        const waveArray = new Uint8Array(10);
        renderSoundWave();
      }
    } else {
      media.current.pause()
      handlePause();
    }
  }

  function handleFullScreen() {
    if (media.current.requestFullscreen) {
      media.current.requestFullscreen();
    } else if (media.current.mozRequestFullScreen) { /* Firefox */
      media.current.mozRequestFullScreen();
    } else if (media.current.webkitRequestFullscreen) { /* Chrome, Safari & Opera */
      media.current.webkitRequestFullscreen();
    } else if (media.current.msRequestFullscreen) { /* IE/Edge */
      media.current.msRequestFullscreen();
    }
  }

  function getAudioWave() {
    //暂停时的图形
    if (!waves.length) {
      let arr = [];
      for (let i = 0; i < 13; i++) {
        i % 2 ? (arr[i] = 35) : (arr[i] = 20);
      }

      return arr.map((height, index) => (
        <div key={index} style={{ height }} className={style['waveform']}></div>
      ));
    }

    //将0-255范围的值映射到8到20的高度范围里
    return waves.map((span, index) => {
      const oldValue = span;
      const oldMin = 0;
      const oldMax = 255;
      const newMin = 10;
      const newMax = 50;

      const height = ((oldValue - oldMin) * (newMax - newMin)) / (oldMax - oldMin) + newMin;

      return <div key={index} style={{ height }} className={style['waveform']} />;
    });
  }

  function getTimestamp() {
    function getTwoDigitsTime(time) {
      return time < 10 ? `0${time}` : time
    }
    const totalTime = `${getTwoDigitsTime(Math.floor(duration / 60))}:${getTwoDigitsTime(duration % 60)}`;
    const currentTime = `${getTwoDigitsTime(Math.floor(currentDuration / 60))}:${getTwoDigitsTime(currentDuration % 60)}`;

    return `${currentTime}/${totalTime}`
  }

  return (
    <>
      {
        type === 'video' ?
        (
          <video src={src} ref={media} width="100%"/>
        ) :
        (
          <div>
            <audio src={src} ref={media} crossOrigin="anonymous"/>
            <div style={{display: 'flex', justifyContent: 'center', alignItems: 'center', height: 380}}>
                {getAudioWave()}
            </div>
          </div>
        )

      }
      <Row style={{marginTop: 25, display: 'flex', alignItems: 'center', position: 'absolute', bottom: -50, width: '100%'}}>
        <Col span={1}>
          <img onClick={handlePlayOrPause} width="100%" src={`./static/images/${isPlaying ? 'pause' : 'play'}.png`} style={{cursor: 'pointer'}} alt=""/>
        </Col>
        <Col span={5} style={{paddingLeft: 20}}>
          {getTimestamp()}
        </Col>
        <Col span={16}>
          <Slider
            value={currentDuration}
            className="slider-wrapper"
            tipFormatter={null}
            max={duration}
            onChange={(value) => {
              media.current.currentTime = value as number;
              setCurrentDuration(value as number);
            }}
          />
        </Col>
        {
          type === 'video' && (
            <Col span={2} style={{paddingLeft: 10}}>
              <Icon onClick={handleFullScreen} type="fullscreen" style={{cursor: 'pointer'}}/>
            </Col>
          )
        }
      </Row>
    </>
  )
}

export default React.forwardRef(MediaPlayer);