import React,  { useState, useEffect, useRef } from 'react';
import { Breadcrumb, Button, Row, Col, Tabs, Tag, Menu, Pagination, Modal, Input, Upload, Icon, message, Slider } from 'antd';
import { Link, RouteComponentProps } from 'react-router-dom';
import Form, { FormComponentProps } from 'antd/lib/form';

import * as API from '@/api';
import DataSetTagsModal from '@/pages/DataSetTagsModal'
import CreateComponentModal from '@/pages/DataSetDetail/CreateComponentModal'
import ComponentForm from '@/pages/DataSetDetail/ComponentForm'
import MediaPlayer from '@/pages/DataSetDetail/MediaPlayer'
import download from '@/util/download';

import getPrefixComponentName from '@/util/getPrefixComponentName';

import './style.module.less';

const { TabPane } = Tabs;

const COMPONENT_TYPES = ['body', 'face', 'audio', 'video', 'feature'];

const PAGE_SIZE = 7;

const DEFAULT_PAGINATIONS = {};

COMPONENT_TYPES.forEach(type => {
  DEFAULT_PAGINATIONS[type] = 1;
});

type Props = {
  isAdmin: boolean;
} & RouteComponentProps<{assetname}> & FormComponentProps

const DataSetDetail: React.FC<Props> = ({match, history, form, isAdmin}) => {
  const [assetInfo, setAssetInfo] = useState(null);
  const [selectedName, setSelectedName] = useState('');
  const [selectedType, setSelectedType] = useState(COMPONENT_TYPES[0]);
  const [showCreateModal, setShowCreateModal] = useState(false);
  const [componentInfo, setComponentInfo] = useState<{url?}>({});
  const [currentPaginations, setCurrentPaginations] = useState(DEFAULT_PAGINATIONS);
  const [showEditTagModal, setShowEditTagModal] = useState(false);
  const [componentInfoTab, setComponentInfoTab] = useState('');
  const [isEditProjectName, setIsEditProjectName] = useState(false);
  const [isReplaceLoading, setisReplaceLoading] = useState(false);
  const [isDownloadAsset, setIsDownloadAsset] = useState(false);
  const [isDownloadComponent, setIsDownloadComponent] = useState(false);

  const { assetname } = match.params;

  const { validateFieldsAndScroll, setFieldsValue } = form;

  async function fetchComponentInfo(componentName) {
    if (!componentName) return;
    const { data } = await API.assets.fetchComponent(assetname, selectedType, componentName);
    // if (selectedType === 'audio') audio.src = data.url;
    // debugger
    setFieldsValue(data);
    setComponentInfo(data);
  }

  async function fetchAsset(isInit = false) {
    const { data } = await API.assets.fetchAsset(assetname);
    if (!data) return;
    const initTypesValue = {};

    COMPONENT_TYPES.forEach(type => {
      initTypesValue[type] = [];
    });

    const asset = {
      ...initTypesValue,
      ...data
    }

    setAssetInfo(asset);
    if (isInit) {
      const bodyName = getPrefixComponentName('body', asset.body[0] || {})
      setSelectedName(bodyName);
    }
  }

  useEffect(() => {
    fetchAsset(true);
  }, []);

  useEffect(() => {
    fetchComponentInfo(selectedName);
  }, [selectedName])

  function getPreview() {
    switch (selectedType) {
      case 'audio':
      case 'video': {
        return (
          <MediaPlayer type={selectedType} src={(componentInfo && componentInfo.url) || ''}/>
        )
      }
      default: {
        return (
          <div style={{width: '100%', textAlign: 'center', lineHeight: '370px'}}>
            不支持预览
          </div>
        )
      }
    }
  }

  function handleClickList(e) {
    setSelectedName(e.key);
  }

  function getFileList(type) {

    return (
      <>
        <Button onClick={() => setShowCreateModal(true)} icon="plus" style={{padding: '0 35px', margin: '10px 0'}}>新增</Button>
        <Menu
          onClick={handleClickList}
          style={{ width: "100%" }}
          defaultSelectedKeys={['1']}
          defaultOpenKeys={['sub1']}
          mode="inline"
          selectedKeys={[selectedName]}
        >
          {console.log(type)}
          {assetInfo[type].slice((currentPaginations[type] - 1) * 7, (currentPaginations[type] - 1) * 7 + 7).map((item, index) =>
            <Menu.Item key={getPrefixComponentName(type, item)}>{getPrefixComponentName(type, item)}</Menu.Item>
          )}
        </Menu>
        <Pagination
          className="component-pagination"
          total={assetInfo[type].length}
          size="small"
          onChange={current => {
            setCurrentPaginations({...currentPaginations, [type]: current});
            setSelectedName(getPrefixComponentName(type, assetInfo[type][(current - 1) * 7]));
          }}
          pageSize={PAGE_SIZE}
          showTotal={() => `共${assetInfo[type].length}项`}
        />
      </>
    )
  }

  async function handleEditUpload(file) {
    const fd = new FormData()
    fd.append('file', file);
    setisReplaceLoading(true);
    const { data } = await API.assets.updateComponentFile(assetname, selectedType, selectedName, fd);
    setisReplaceLoading(false);
    if (!data) return message.error('替换失败');
    // debugger
    message.success('替换成功');
    setFieldsValue({url: data.url})
  }

  function getComponentFields() {
    return (
      <Tabs type="card" className="component-tabs-wrapper" onChange={tab => setComponentInfoTab(tab)}>
        <TabPane tab="预览" key="预览" >
          <div style={{padding: 20, height: 390, background: '#f0f3ff', position: 'relative'}}>
            {getPreview()}
          </div>
        </TabPane>
        <TabPane tab="编辑" key="编辑" forceRender={true}>
          <div style={{padding: 20, height: 390, background: '#f0f3ff', position: 'relative', overflow: 'auto'}}>
            <ComponentForm
              form={form}
              type={selectedType}
              isCreate={false}
              onEditUpload={handleEditUpload}
              isReplaceLoading={isReplaceLoading}
            />
          </div>
          {componentInfoTab === '编辑' && <Button onClick={() => submitComponentInfo()} style={{float: 'right', marginTop: 20}}>刷新应用</Button>}
        </TabPane>
      </Tabs>
    )
  }

  async function submitComponentInfo() {
    validateFieldsAndScroll( async (err, values) => {
      if (err) return;
      const { data } = await API.assets.updateComponent(assetname, selectedType, selectedName, values);
      if (!data) return;
      message.success('编辑成功');
      fetchAsset();
    })
  }

  console.log(assetInfo);

  function handleDelete(isComponent) {
    async function deleteAsset() {
      const { data } = isComponent ? await API.assets.deleteComponent(assetname, selectedType, selectedName) : await API.assets.deleteAsset(assetname);
      // if (!data) return;
      setSelectedName('');
      message.success('删除成功');
      isComponent && fetchAsset();
      !isComponent && history.push('/dataset');
    }

    Modal.confirm({
      okText: '确定',
      cancelText: '取消',
      title: `确定要删除${isComponent ? selectedName : assetname}吗`,
      onOk() {
        deleteAsset()
      },
      onCancel() {},
    });
  }

  function handleCreateSuccess(data) {
    setShowCreateModal(false);
    setAssetInfo({
      ...assetInfo,
      ...{[selectedType]: data[selectedType]}
    })

  }

  if (!assetInfo) return null;

  const { projectname, actorname, createtime, tags, comment } = assetInfo;

  const formItemLayout = {
    labelCol: {
      xs: { span: 24 },
      sm: { span: 4 }
    },
    wrapperCol: {
      xs: { span: 24 },
      sm: { span: 20 }
    }
  };

  async function handleChangeProjectName(e) {
    const projectname = e.target.value;
    if (!projectname) {
      message.error('请填写项目名称');
      setIsEditProjectName(false);
      return
    }
    const { data } = await API.assets.updateAsset(assetname, { projectname });

    message.success('编辑成功');
    setAssetInfo({...assetInfo, projectname });
    setIsEditProjectName(false);
  }

  async function handleDownloadComponent() {
    if (isDownloadComponent) return;
    setIsDownloadComponent(true);
    const result = await API.assets.downloadComponent(assetname, selectedType, selectedName);
    setIsDownloadComponent(false);
    if (!result) return;
    download(result)
  }

  async function handleDownloadAsset() {
    setIsDownloadAsset(true);
    const result = await API.util.downloadAsset({assetname});
    setIsDownloadAsset(false);
    if (!result) return;
    download(result);
  }

  return (
    <>
      <Breadcrumb style={{marginBottom: 30}}>
        <Breadcrumb.Item><Link to="/dataset">数据集</Link></Breadcrumb.Item>
        <Breadcrumb.Item>AssetName详情页</Breadcrumb.Item>
      </Breadcrumb>
      <div style={{background: '#fff', padding: 20, display: 'flex', position: 'relative', marginBottom: 20}}>
        <div style={{position: 'absolute', right: 20, top: 20}}>
          <Button type="danger" ghost style={{width: 95}} onClick={handleDelete}>删除</Button>
          <Button type="primary" ghost style={{width: 95, marginRight: 10, marginLeft: 10}} onClick={() => setShowEditTagModal(true)}>标签管理</Button>
          <Button type="primary" style={{width: 95}} onClick={handleDownloadAsset} loading={isDownloadAsset}>下载</Button>
        </div>
        <div style={{width: 200, height: 200, overflow: 'hidden', marginRight: 20, background: '#fafafa'}}>
          <img src={assetInfo.preview} height="100%" alt="" style={{marginLeft: '50%', transform: 'translateX(-50%)'}}/>
        </div>
        <div style={{flex: 1}}>
          <div style={{height: 200, display: 'flex', flexDirection: 'column', justifyContent: 'space-between'}}>
            <div>{assetname}</div>
            <div>
              <span>
                项目名称：
                {
                  isEditProjectName ? (
                    <Input
                      size="small"
                      defaultValue={projectname}
                      style={{width: 200}}
                      onPressEnter={(e) => handleChangeProjectName(e)}
                      onBlur={(e) => handleChangeProjectName(e)}
                    />
                  ) : projectname
                }
              </span>
              <Icon type="form" style={{ marginLeft: 10, cursor: 'pointer' }} onClick={() => setIsEditProjectName(true)}/>
              </div>
            <div>{actorname}{actorname ? '，' : ''}{createtime}</div>
            <div style={{height: 100, background: '#fafafa', padding: 10, overflow: 'auto', marginBottom: 10}}>
              {tags.map(tag => (
                <Tag key={tag}>{tag}</Tag>
              ))}
            </div>
          </div>
          <div>{comment}</div>
        </div>
      </div>

      <Row>
        <Col span={11}>
          <div style={{background: '#fff', marginRight: 20, padding: 20, height: 560}}>
            <Tabs
              className="component-list-wrapper"
              defaultActiveKey="1"
              onChange={type => {
                setSelectedType(type);
                setSelectedName(assetInfo[type][0] && getPrefixComponentName(type, assetInfo[type][0]));
              }}>
              {
                COMPONENT_TYPES.map(type => (
                  <TabPane
                    tab={type.charAt(0).toUpperCase() + type.slice(1)}
                    key={type}
                    style={{paddingBottom: 50, height: 452}}
                  >
                    {getFileList(type)}
                    {/* {getPagination(type)} */}
                  </TabPane>
                ))
              }
            </Tabs>
          </div>
        </Col>
        <Col span={13} style={{background: '#fff', padding: 20, height: 560}}>
          {
            selectedName && (
              <>
                <p>{selectedName}</p>
                <span style={{float: 'right', marginTop: -30}}>
                  <Icon type="download" style={{fontSize: 20, marginRight: 15, cursor: 'pointer'}} onClick={handleDownloadComponent}/>
                  <Icon type="delete" style={{fontSize: 20, cursor: 'pointer'}} onClick={() => handleDelete(true)}/>
                </span>
                <span></span>
                {getComponentFields()}
              </>
            )
          }

        </Col>
      </Row>

      {
        showCreateModal && (
          <CreateComponentModal
            assetname={assetname}
            type={selectedType}
            onSuccess={handleCreateSuccess}
            onCancel={() => setShowCreateModal(false)}
          />
        )
      }

      {
        showEditTagModal && (
          <DataSetTagsModal
            defaultTags={assetInfo.tags}
            isMutipleTag={false}
            onSuccess={fetchAsset}
            onClose={() => setShowEditTagModal(false)}
            editTagAssetName={assetname}
          />
        )
      }
    </>
  )
}

export default Form.create<Props>()(DataSetDetail);