import React, { useState, useRef } from 'react';
import { Modal, Select, message } from 'antd';
import { Link } from 'react-router-dom';

import * as API from '@/api';

const DataSetTagsModal = ({isMutipleTag, onClose, onSuccess, defaultTags = [], checkedList = [], editTagAssetName}) => {
  const [editTags, setEditTags] = useState(defaultTags);
  const [searchTags, setSearchTags] = useState([]);
  const [showSearchList, setShowSearchList] = useState(false);
  const [isLoading, setIsLoading] = useState(false);

  const searchTimer = useRef(null)

  async function handleTagsOk() {
    setIsLoading(true);
    const { data } = isMutipleTag ? await API.assets.addTags({ assetnames: checkedList, tags: editTags}) : await API.assets.updateTags(editTagAssetName, { tags: editTags });
    setIsLoading(false);
    if (!data) return;
    isMutipleTag ? message.success('标签添加成功') : message.success('标签修改成功');
    onSuccess();
    onClose();
  }

  async function handleSearchTag(name) {
    name ? setShowSearchList(true) : setShowSearchList(false);
    if (searchTimer.current) clearTimeout(searchTimer.current)
    searchTimer.current = setTimeout(async () => {
      const { data } = await API.assets.searchTags({ name })
      if (!data) return;
      setSearchTags(data);
    }, 500);
  }

  return (
    <Modal
        title={isMutipleTag ? '批量添加标签' : '管理标签'}
        onCancel={() => onClose()}
        style={{paddingTop: 30, paddingBottom: 30}}
        visible={true}
        width={680}
        maskClosable={false}
        onOk={handleTagsOk}
        okText="保存"
        cancelText="取消"
        confirmLoading={isLoading}
      >
        <p>{isMutipleTag ? (`请为 ${checkedList.slice(0, 2).join('，')} 等${checkedList.length}个 Asset 添加标签：`) : `${editTagAssetName}标签`}</p>
        <Select
          mode="tags"
          value={editTags}
          open={showSearchList}
          className="add-tags-wrapper"
          style={{ width: '100%' }}
          placeholder="请输入标签"
          autoClearSearchValue={true}
          dropdownRender={menu => (
            <div>
              {menu}
            </div>
          )}
          notFoundContent={null}
          onChange={value => {
            setEditTags(value);
            setSearchTags([]);
            setShowSearchList(false);
          }}
          onFocus={() => setSearchTags([])}
          filterOption={(inputValue, option) => {
            return true
          }}
          onSearch={handleSearchTag}
        >
          {searchTags.map(tag => (
            <Select.Option key={tag}>
              {tag}
            </Select.Option>
          ))}
        </Select>
      </Modal>
  )
}

export default DataSetTagsModal