import React, { useState, useEffect, useRef } from 'react';
import { DatePicker, Input, Button, List, Pagination, Upload, message, Progress, Row, Col, Tag, Icon, Tooltip } from 'antd'
import moment from 'moment';

import * as API from '@/api'
import UploadingList from '@/pages/UploadManagement/UploadingList'

import style from './style.module.less';

import Axios from '@fuxi/axios-pro';

import getPrefixComponentName from '@/util/getPrefixComponentName';

const CancelToken = Axios.CancelToken;

const FILE_TYPES = ['body', 'face', 'feature', 'audio', 'video', 'preview'];

const UploadManagement = ({isAdmin}) => {
  const [page, setPage] = useState(1);
  const [num, setNum] = useState(10);
  const [list, setList] = useState([]);
  const [uploadingList, setUploadingList] = useState([]);
  const [search, setSearch] = useState('');
  const [total, setTotal] = useState(0);
  const [isReadingFiles, setIsReadingFiles] = useState(false);
  const [selectedTime, setSelectedTime] = useState(moment().format("YYYY-MM-DD"));

  const files = useRef({});
  const uploadingFilesRef = useRef([]);
  const isMultipleRef = useRef(false);
  const isReadingFilesRef = useRef(false);
  const cancelRef = useRef(null);
  const isFetchList = useRef(false);

  const totalNum = total + uploadingList.length;

  async function fetchUploadList() {
    if (isFetchList.current) {
      cancelRef.current && cancelRef.current();
    }

    const params = {
      createtime__lt: selectedTime,
      assetname: search,
      _page: page,
      _num: num,
      _offset: uploadingFilesRef.current.length
    }

    const cancelToken = new CancelToken( c => cancelRef.current = c);

    try {
      isFetchList.current = true;
      const { data } = await API.upload.fetchUploadList(params, cancelToken);
      isFetchList.current = false;
      const { items, total } = data;
      setList(items);
      setTotal(total);
    } catch(e) {
      console.log('request canceled');
    }

  }

  useEffect(() => {
    fetchUploadList();
  }, [page, num])

  useEffect(() => {
    const lastPage = Math.ceil(totalNum / num);
    if (!lastPage) return;
    if (page > lastPage) setPage(lastPage);
  }, [totalNum])

  function handleDateChange(e) {
    setSelectedTime(e.format("YYYY-MM-DD"));    
  }

  function readFileInfo(file, folderName) {
    function onReaderLoad(event) {
      //alert(event.target.result);
      const fileInfo = JSON.parse(event.target.result);
      // debugger
      const currentFiles = files.current[getRootFolderName(file)]

      validteFilesAndUpload(fileInfo, currentFiles, folderName);
    }

    const reader = new FileReader();
    reader.onload = onReaderLoad;
    reader.readAsText(file);
  }

  function handleUpload(fileInfo, folderName) {
    function clearUpdatedFiles() {
      //清除正在上传的缓存
      delete files.current[folderName];
    }

    const list = [{
      assetname: fileInfo.assetname,
      files: files.current[folderName].files,
      infoFile: files.current[folderName].infoFile
    }, ...uploadingFilesRef.current ]

    if (Object.keys(files.current).length === 1 && isMultipleRef.current === true && isReadingFilesRef.current === true) {
      isReadingFilesRef.current = false;
      setIsReadingFiles(false);
    }

    setUploadingList(list);
    uploadingFilesRef.current = list;
    clearUpdatedFiles();
  }

  function validteFilesAndUpload(fileInfo, currentFiles, folderName) {
    // debugger
    function isExist(name) {
      const exist = currentFiles.files.some(file => {
        return name === file.name;
      });

      if (!exist) handleFileNotFound(name);
      return exist;
    }

    function handleFileNotFound(name) {
      // message.error(isMultipleRef.current ? `${1}` : `文件${name}不存在`, 8);
      message.error(`文件夹${folderName}下不存在文件${name}`, 10);
    }

    function getFileName(name, url) {
      const lastIndex = url.lastIndexOf('.');
      const fileType = url.substring(lastIndex);

      return name + fileType;
    }

    //根据info.json文件进行检测，查看是否缺少
    for (let type of FILE_TYPES) {
      if (!fileInfo[type]) continue;

      if (type === 'preview') {
        if (!isExist(fileInfo[type])) return;
        continue;
      }

      for (let item of fileInfo[type]) {
        const {model, format, name, samplerate, url} = item;
        switch(type) {
          case 'body': 
          case 'face': {
            const prefixName = getPrefixComponentName(type, item);
            const fileName = getFileName(prefixName, url);
            if (!isExist(fileName)) return;
            break;
          }

          case 'audio': {
            const prefixName = getPrefixComponentName(type, item);
            const fileName = getFileName(prefixName, url);
            if (!isExist(fileName)) return;
            break;
          }
          case 'video': {
            const prefixName = getPrefixComponentName(type, item);
            const fileName = getFileName(prefixName, url);
            if (!isExist(fileName)) return;
            break;
          }
          case 'feature': {
            const prefixName = getPrefixComponentName(type, item);
            const fileName = getFileName(prefixName, url);
            if (!isExist(fileName)) return;
            break;
          }
        }
      }
    }

    handleUpload(fileInfo, folderName);
  }



  function getRootFolderName(file) {
    const path = file.webkitRelativePath
    const startIndex = isMultipleRef.current ? (path.indexOf('/') + 1) : 0;
    const endIndex = isMultipleRef.current ? path.indexOf('/', startIndex) : path.indexOf('/');
    return path.substring( startIndex, endIndex );
  }

  function processFile(file) {
    if (file.name === '.DS_Store') return;
    const rootFolderName = getRootFolderName(file);
    const currentFiles = files.current[rootFolderName] || {};

    if (file.name === 'info.json') {
      readFileInfo(file, rootFolderName);
      files.current[rootFolderName] = {...currentFiles, infoFile: file};
      return;
    }

    files.current[rootFolderName] = {...currentFiles, files: [...(currentFiles.files || []), file]};
  }

  function handleUploadSuccess(assetname) {
    function removeUploadingList() {
      const list = uploadingFilesRef.current.filter(item => item.assetname !== assetname);

      uploadingFilesRef.current = list;
      setUploadingList(list);
    }

    fetchUploadList();
    removeUploadingList()
  }

  function handleSearch() {
    fetchUploadList()
  }

  const uploadProps = {
    directory: true,
    showUploadList: false,
    beforeUpload: file => {
      if (isMultipleRef.current && isReadingFilesRef.current === false) {
        setIsReadingFiles(true);
        isReadingFilesRef.current = true;
      }

      processFile(file);
      return false;
    }
  };

  const currentPageStartIndex = (page - 1) * num;
  const currentPageEndIndex = currentPageStartIndex + num;

  return(
    <div className={style["upload-wrapper"]}>
      <div style={{display: 'flex', justifyContent: 'space-between'}}>
        <DatePicker
          defaultValue={moment()}
          allowClear={false}
          disabledDate={currentDate => currentDate > moment()}
          onChange={handleDateChange}
          style={{ width: 240 }}
        />
        <Input.Search
          placeholder="搜索"
          onChange={e => setSearch(e.target.value)}
          style={{ width: 240, marginLeft: 15 }}
          onSearch={handleSearch}
        />
        <div style={{flex: 1}} />
        <Upload {...uploadProps} disabled={isReadingFiles}>
          <Button
            type="primary"
            style={{ width: 150 }}
            loading={isReadingFilesRef.current}
            onClick={() => isMultipleRef.current = true}
          >
            {isReadingFiles ? '文件读取中' : '批量上传Asset'}
          </Button>
        </Upload>
        <Upload {...uploadProps}>
          <Button type="primary" style={{ marginLeft: 20, width: 150 }} onClick={() => isMultipleRef.current = false}>上传Asset</Button>
        </Upload>
      </div>

      <List
        size="large"
        style={{background: '#fff',marginTop: 20}}
      >
        {
          uploadingList.map((item, index) => (
            <UploadingList
              key={item.assetname}
              assetname={item.assetname}
              handleUploadSuccess={handleUploadSuccess}
              files={item.files}
              infoFile={item.infoFile}
              show={index >= currentPageStartIndex && index <= currentPageEndIndex}
            />
          ))
        }

        {
          list.map(item => (
            <List.Item key={item.assetname + item.createtime}>
            <Row style={{ width: '100%', display: 'flex', alignItems: 'center' }}>
              <Col span={4}>
                <div>{item.assetname}</div>
                <div>{`${item.projectname}${item.projectname ? '，' : ''}${item.component_num} compoments`}</div>
              </Col>
              <Col span={8} style={{textAlign: 'center'}}>
                {item.createtime}
              </Col>
              <Col span={8}>
                <Tag color={item.status === 'error' ? "#ffd4d6" : "#d1f5eb"}><span style={{color: item.status === 'error' ? '#ff535b' : '#009966'}}>{item.status === 'error' ? '上传失败' : '上传成功'}</span></Tag>
                {
                  item.status === 'error' && (
                    <>
                      <Tooltip overlay={item.msg}>
                        <Icon type="question-circle" style={{color: 'red', cursor: 'pointer'}}/>
                      </Tooltip>
                      <span style={{marginLeft: 10}}>{item.msg.length > 16 ? `${item.msg.substring(0, 16)}...` : item.msg}</span>
                    </>
                  )
                }
              </Col>
              <Col offset={2} span={2}>
                {
                  item.status === 'error' && (
                    <Upload {...uploadProps}>
                      <span onClick={() => isMultipleRef.current = false} style={{cursor: 'pointer', color: '#3E69FA'}}>重新上传</span>
                    </Upload>
                  )
                }
              </Col>
            </Row>
          </List.Item>
          ))
        }
      </List>

      <Pagination
        showQuickJumper
        showSizeChanger
        current={page}
        onChange={page => setPage(page)}
        onShowSizeChange={(page, size) => {
          setPage(page);
          setNum(size);
        }}
        total={totalNum}
        style={{float: "right", marginTop: 30}}
      />
    </div>
  )
}

export default UploadManagement;