import React, { useEffect, useRef, useState } from 'react';
import { Modal, message } from 'antd';
import Form, { FormComponentProps } from 'antd/lib/form';
import ComponentForm from '@/pages/DataSetDetail/ComponentForm';
import * as API from '@/api'

import getPrefixComponentName from '@/util/getPrefixComponentName'

type Props = {
  assetname: string;
  type: string;
  onCancel(): void;
  onSuccess(data): void;
} & FormComponentProps

const CreateComponentModal: React.FC<Props> = ({assetname, type, form, onCancel, onSuccess }) => {
  const [isLoading, setIsLoading] = useState(false)

  const { validateFieldsAndScroll } = form;
  function handleOk() {
    validateFieldsAndScroll( async (err, values) => {

      if (err) return;
      const fd = new FormData();
      for (let name of Object.keys(values)) {
        const isFile = name === 'file'
        isFile ? fd.append(name, values[name].file) : fd.append(name, values[name]);
        if (isFile) {
          const file = values[name].file;

          if (!values.name && values.format && !values.model) {
            const fileType = file.name.substring(file.name.lastIndexOf('/') + 1);
            if (fileType !== values.format) message.error('文件类型不匹配');
            return 
          }

          const validFileName = getPrefixComponentName(type, values);
          const fileName = file.name.substring(0, file.name.lastIndexOf('.'));

          debugger

          if (validFileName !== fileName) return message.error('文件名不匹配');
        }
      }
  
      setIsLoading(true);
      const { data } = await API.assets.addComponent(assetname, type, fd);
      setIsLoading(false);
      message.success('新增成功');
      onSuccess(data);
    })
  }

  return (
    <Modal
      className="component-modal-wrapper"
      title="新增"
      width={600}
      visible={true}
      maskClosable={false}
      onOk={handleOk}
      onCancel={onCancel}
      okText="保存"
      cancelText="取消"
      confirmLoading={isLoading}
    >
      <ComponentForm
        form={form}
        type={type}
        isCreate
      />
    </Modal>
  )
}

export default Form.create<Props>()(CreateComponentModal);