import React, {useState, useEffect, useRef} from 'react';
import { withRouter, RouteComponentProps } from 'react-router-dom'
import queryString from 'query-string';
import { Select, Row, Col, Input, Tabs, Menu, Dropdown, Icon, Button, Tag, Divider, Tooltip, DatePicker } from 'antd';
import AceEditor from "react-ace";
import moment, { unitOfTime } from 'moment';

import style from './style.module.less';
import "ace-builds/src-noconflict/mode-json";

const { TabPane } = Tabs;
const { RangePicker } = DatePicker;

interface Props {
  wrapperStyle: React.CSSProperties;
  isHomePage: boolean;
  handleFilter?(params): void;
}

const FIELDS_OPTIONS = [
  'assetname',
  'tags',
  'uploader',
  'comment',
  'createtime',
  'projectname',
  'preview',
  'body.model',
  'body.format',
  'body.comment',
  'body.fps',
  'body.framenum',
  'body.startframe',
  'body.endframe',
  'body.orderformat',
  'body.coordinate',
  'body.url',
  'audio.name',
  'audio.format',
  'audio.samplerate',
  'audio.url',
  'video.name',
  'video.format',
  'video.url',
  'feature.name',
  'feature.url',
]

const FilterSearchBar: React.FC<Props & RouteComponentProps> = ({wrapperStyle = {}, isHomePage, history, location, handleFilter}) => {
  const [isOpen, setIsOpen] = useState(false);
  const [showFilterDropdown, setShowFilterDropdown] = useState(false);
  const [selectedTime, setSelectedTime] = useState('Today');
  const [filterMode, setFilterMode] = useState('Filter Values');
  const [editorValue, setEditorValue] = useState('{}');
  const [filters, setFilters] = useState([]);

  const [inputValue, setInputValue] = useState('');
  const [editingInputValue, setEditingInputValue] = useState('');
  const [fieldValue, setFieldValue] = useState(null);
  const [operatorValue, setOperatorValue] = useState(null);
  const [filterValue, setFilterValue] = useState(null);
  const [labelValue, setLabelValue] = useState('');
  const timeRangeRef = useRef({})

  const editIndexRef = useRef(null);


  const inputHeight = isHomePage ?  '50px' : '40px';

  function getDropdownRender() {

    function handleTimeClick(time: string) {
      setSelectedTime(time);
      setIsOpen(false);
    }


    function getTimeList(list: string[]) {
      return (
        <ul>
          {list.map(time =>
            <li className={style['list-item']} onClick={() => handleTimeClick(time)} key={time}>{time}</li>
          )}
        </ul>
      )
    }

    function handleRangeOk(range) {
      setIsOpen(false);
      setSelectedTime('Absolute');
      const [startTime, endTime] = range;
      timeRangeRef.current = {
        startTime,
        endTime
      }
    }

    return (
      <div
        className={style['dropdown-menu']}
        style={{width: 600, background: '#f5f5f5', marginTop: 10}}
        onMouseDown={e => e.stopPropagation()}
        onClick={e => e.stopPropagation()}
      >
        <Tabs defaultActiveKey="1">
          <TabPane tab="Quick" key="1">
            <Row>
              <Col span={6}>
                {getTimeList(['Today', 'This week', 'This mouth'])}
              </Col>
              <Col span={6}>
                {getTimeList(['Last 1 hour', 'Last 12 hour', 'Last 24 hour'])}
              </Col>
            </Row>
          </TabPane>
          <TabPane tab="Absolute" key="2">
          <RangePicker
            style={{width: '100%', margin: '20px 0'}}
            showTime={{
              hideDisabledOptions: true,
              defaultValue: [moment('00:00:00', 'HH:mm:ss.SSS'), moment('11:59:59', 'HH:mm:ss.SSS')],
            }}
            onOk={handleRangeOk}
            format="YYYY-MM-DDTHH:mm:ss.SSSZ"
          />
          </TabPane>
        </Tabs>
      </div>
    )
  }

  function closeFilterModal() {
    function clearInputsValue() {
      setFieldValue(null);
      setOperatorValue(null);
      setFilterValue(null);
      setLabelValue('');
    }
    setEditorValue('{}')
    setFilterMode('Filter Values');
    setShowFilterDropdown(false);
    editIndexRef.current = null;
    clearInputsValue();
  }

  function handleDropDownClick(e: React.MouseEvent<HTMLDivElement, MouseEvent>) {
    e.stopPropagation();
    setIsOpen(!isOpen);
    closeFilterModal();
  }

  function isDisableSaveButton() {
    if (filterMode === 'Filter Values') {
      if (!fieldValue || !operatorValue || (!filterValue && !['exists', 'does not exist'].includes(operatorValue))) return true;
      if (['is one of', 'is not one of'].includes(operatorValue) && !filterValue.length) return true;
      return false;
    } else {
      try {
        JSON.parse(editorValue);
        return false;
      } catch(e) {
        return true;
      }
    }
  }

  function handleSaveFilter(e) {
    e.stopPropagation();

    function getInputsFilter(inputsValue) {
      return {
        isEditorValue: false,
        ...inputsValue,
        editorValue: convertToEsQuery(inputsValue)
      }
    }

    let filter;

    if (filterMode === 'Filter Values') {
      const inputsValue = {
        fieldValue,
        operatorValue,
        filterValue,
        labelValue
      }
      filter = getInputsFilter(inputsValue);
    } else {
      const queryValue = JSON.parse(editorValue).query || {}

      filter = {
        isEditorValue: true,
        editorValue: queryValue,
        labelValue
      }

      if (queryValue.match) {
        const fieldValue = Object.keys(queryValue.match)[0];

        if (fieldValue && queryValue.match[fieldValue].query) {
          const inputsValue = {
            fieldValue,
            operatorValue: 'is',
            filterValue: queryValue.match[fieldValue].query,
          }
          filter = getInputsFilter(inputsValue);
        }
      }

      if (queryValue.exists) {
        const fieldValue = Object.keys(queryValue.match)[0];

        if (fieldValue && queryValue.exists[fieldValue].field) {
          const inputsValue = {
            fieldValue,
            operatorValue: 'exists',
            filterValue: queryValue.exists[fieldValue].field
          }
          filter = getInputsFilter(inputsValue);
        }
      }
    }

    if (editIndexRef.current !== null) {
      //检查是否是在编辑
      filter.isExclude = filters[editIndexRef.current].isExclude;
      filters.splice(editIndexRef.current, 1, filter);
      setFilters([...filters]);
    } else {
      setFilters([...filters, filter]);
    }

    closeFilterModal();
  }

  function getFilterDropdown() {
    function handleFilterModeChange(key: string) {
      setFilterMode(key)
    }

    function getFilterDropdownContent() {
      
      return (
        <>
          <Row style={{marginTop: 10, display: filterMode === "Filter Values" ? 'block' : 'none'}}>
            <Col span={5} style={{paddingRight: 5}}>
              <Select
                size="large"
                value={fieldValue || []}
                onChange={field => setFieldValue(field)}
                placeholder="Field"
                style={{width: '100%', marginRight: 5}}
              >
                {
                  FIELDS_OPTIONS.map(item => (
                    <Select.Option key={item}>
                      {item}
                    </Select.Option>
                  ))
                }
              </Select>
            </Col>
            <Col span={5} style={{paddingRight: 5}}>
              <Select
                size="large"
                value={operatorValue || []}
                onChange={operator =>{
                  setOperatorValue(operator);
                  setFilterValue(null);
                }}
                placeholder="Operator"
                style={{width: '100%', marginRight: 5}}
              >
                {
                  ['is', 'is not', 'is one of', 'is not one of', 'exists', 'does not exist'].map(item => (
                    <Select.Option key={item}>
                      {item}
                    </Select.Option>
                  ))
                }
              </Select>
            </Col>
            <Col span={14}>
              {
                (operatorValue === 'is' || operatorValue === 'is not') && (
                    <Input
                      size="large"
                      value={filterValue}
                      placeholder="Values..."
                      onChange={e => setFilterValue(e.target.value)}
                    />
                )               
              }

              {
                (operatorValue === 'is one of' || operatorValue === 'is not one of') && (
                    <Select
                      size="large"
                      mode="tags"
                      value={filterValue || []}
                      style={{width: '100%'}}
                      placeholder="Values..."
                      onChange={value => setFilterValue(value)}
                    />
                )               
              }
            </Col>
          </Row>
          <AceEditor
            mode="json"
            setOptions={{ useWorker: false }}
            value={editorValue}
            showPrintMargin={false}
            onChange={value => setEditorValue(value)}
            name="UNIQUE_ID_OF_DIV"
            style={{height: 200, width: '100%', display: filterMode === "Filter Values" ? 'none' : 'block'}}
            editorProps={{ $blockScrolling: true }}
          />
        </>
      ) 
    }

    const menu = (
      <Menu onClick={({key}) => handleFilterModeChange( key )}>
        <Menu.Item key="Filter Values">
          Filter Values
        </Menu.Item>
        <Menu.Item key="Query DSL">
          Query DSL
        </Menu.Item>
      </Menu>
    );

    return (
      <div className={style['filter-dropdown-wrapper']} onClick={e => e.stopPropagation()}>
        <div style={{display: 'flex', justifyContent: 'space-between'}}>
          <span>Filter</span>
          <Dropdown overlay={menu} trigger={['click']}>
            <span style={{cursor: 'pointer'}}>
              {filterMode}<Icon type="down" />
            </span>
          </Dropdown>
        </div>
        {getFilterDropdownContent()}
        <div style={{textAlign: 'left', padding: '20px 0'}}>Label</div>
        <Row>
          <Col span={5}>
            <Input value={labelValue} size="large" placeholder="Optional" onChange={e => setLabelValue(e.target.value)}></Input>
          </Col>
        </Row>
        <div style={{display: 'flex', justifyContent: 'flex-end'}}>
          <Button type="primary" ghost style={{marginRight: 10}} onClick={closeFilterModal}>Cancle</Button>
          <Button type="primary" disabled={isDisableSaveButton()} onClick={handleSaveFilter}>Save</Button>
        </div>
      </div>
    )
  }

  function handleFilterClick(e: React.MouseEvent<HTMLDivElement, MouseEvent>) {
    e.stopPropagation();
    setIsOpen(false);
    setShowFilterDropdown(!showFilterDropdown);

  }

  useEffect(() => {
    const { date, filterList, inputValue, timeRange } = queryString.parse(location.search) || {};
    
    try {
      const filters = JSON.parse(filterList);
      setFilters(filters);
    } catch(e) {
      console.log(e)
    }

    date && setSelectedTime(date);
    inputValue && setInputValue(inputValue);
    timeRange && (timeRangeRef.current = timeRange);
  }, [])

  useEffect(() => {
    function handleClickOutSide() {
      setIsOpen(false);
      closeFilterModal();
    }

    window.addEventListener('click', handleClickOutSide);

    return () => {
      window.removeEventListener('click', handleClickOutSide);
    }
  }, [])

  function converToEditorValue(filterInputValue) {
    const { fieldValue, operatorValue, filterValue} = filterInputValue;
    let value;

    switch(operatorValue) {
      case 'is':
      case 'is not': {
        value = {
          query: {
            match: {
              [fieldValue]: {
                query: filterValue,
                type: "phrase"
              }
            }
          }
        }

        setEditorValue(JSON.stringify(value, null, 2));
      }

      case '': {

      }

      case '': {
        
      }
    }
  }

  function getFilterTag(filter, index) {
    function getTagBackground(isNot) {
      if (filter.isExclude) {
        return isNot ?  'repeating-linear-gradient(45deg, rgba(144, 0, 2, 0.3), rgba(144, 0, 2, 0.3) 10px, rgba(144, 0, 2, 0.5) 10px, rgba(144, 0, 2,0.5) 20px)' : 'repeating-linear-gradient(45deg,#7eb7cb,#7eb7cb 10px,#4f9cb8 10px,#4f9cb8 20px)';
      } else {
        return isNot ?  "rgb(144, 0, 2)" : "rgb(14, 101, 147)"
      }
    }

    function getTag(value, isNot = false) {
      return (
        <Tag
          key={value + index}
          // closable
          className={style["filter-tag"]}
          color={isNot ?  "rgba(144, 0, 2, 0.8)" : "rgba(14, 101, 147, 0.8)"}
          style={{background: getTagBackground(isNot)}}
        >
          {`${isNot ? 'NOT ' : ''} ${value}`}
          <div className={style["filter-tag-icon-wrapper"]} style={{ background: isNot ? "rgba(194, 57, 49, 0.8)" : "rgba(14, 101, 147, 0.8)"}}>
            <Tooltip overlay={filter.isExclude ? "Include Matches" : "Exclude Matches"}>
              <Icon
                type={filter.isExclude ? "zoom-in" : "zoom-out"}
                style={{ fontSize: 15, cursor: 'pointer' }}
                onClick={() => {
                  filter.isExclude = !filter.isExclude;
                  setFilters([...filters]);
                }}
              />
            </Tooltip>
            
            <Divider type="vertical"/>
            <Tooltip overlay="Delete">
              <Icon
                type="delete"
                style={{ fontSize: 15, cursor: 'pointer' }}
                onClick={() => setFilters(filters.filter(item => item !== filter))}
              />
            </Tooltip>
            
            <Divider type="vertical"/>

            <Tooltip overlay="Edit">
              <Icon
                type="form"
                style={{ fontSize: 15, cursor: 'pointer' }}
                onClick={(e) => {
                  e.stopPropagation();
                  editIndexRef.current = index;
                  setShowFilterDropdown(true);

                  if (filter.isEditorValue) {
                    setEditorValue(JSON.stringify({query: filter.editorValue}, null, 2));
                    setFilterMode('Query DSL');
                    setLabelValue(filter.labelValue);
                    return;
                  }

                  let editorValue;

                  if (filter.editorValue.match_phrase) {
                    editorValue = {query: {match: filter.editorValue.match_phrase}}
                  } else {
                    editorValue = {query: filter.editorValue}
                  }

                  setEditorValue(JSON.stringify(editorValue, null, 2))
                  setFieldValue(filter.fieldValue);
                  setOperatorValue(filter.operatorValue);
                  setFilterValue(filter.filterValue);
                  setLabelValue(filter.labelValue);

                }}
              />
            </Tooltip>
          </div>
        </Tag>
      )
    }

    const { fieldValue, operatorValue, filterValue } = filter;

    if (filter.labelValue) {
      const falseOperator = ['is not', 'is not one of', 'does not exist'];
      return getTag(filter.labelValue, falseOperator.includes(operatorValue));
    }

    if (filter.isEditorValue) {
      const value = JSON.stringify(filter.editorValue).replace(/\s/g, '')
      return getTag(value)
    } else {
      // 'is', 'is not', 'is one of', 'is not one of', 'exists', 'does not exist'
      switch (operatorValue) {
        case 'is':
          return getTag(`${fieldValue}: ${filterValue}`);
        case 'is not':
          return getTag(`${fieldValue}: ${filterValue}`, true);
        case 'is one of':
          return getTag(`${fieldValue} ${operatorValue} ${filterValue.join(',')}`);
        case 'is not one of':
          return getTag(`${fieldValue} is one of ${filterValue.join(',')}`, true);
        case 'exists':
          return getTag(`${fieldValue} ${operatorValue}`);
        case 'does not exist':
          return getTag(`${fieldValue} exists`, true);
      }
    }
  }

  function convertToEsQuery(inputsValue) {
    const { fieldValue, operatorValue, filterValue} = inputsValue;
    let esQuery;

    switch(operatorValue) {
      case 'is':
      case 'is not': {
        esQuery = {match_phrase: {[fieldValue]: {query: filterValue}}}
        break;
      }

      case 'is one of':
      case 'is not one of': {
        esQuery = {
          bool: {
            minimum_should_match: 1,
            should: filterValue.map(value => ({match_phrase: {[fieldValue]: value}}))
          }
        }
        break;
      }

      case 'exists':
      case 'does not exist': {
        esQuery = {exists: {field: fieldValue}}
        break;
      }
    }

    return esQuery
  }

  function getFilterData() {
    const filter = {
      bool: {
        filter: [],
        must: [],
        must_not: [],
        should: []
      }
    }

    filters.forEach(item => {
      if (item.isExclude) return;

      if (item.isEditorValue) {
        filter.bool.filter.push(item.editorValue)
      } else {
        const { fieldValue, operatorValue, filterValue} = item;

        switch(operatorValue) {
          case 'is':
          case 'is not': {
            const value = {match_phrase: {[fieldValue]: {query: filterValue}}}
            operatorValue === 'is' ? filter.bool.filter.push(value) : filter.bool.must_not.push(value);
            break;
          }

          case 'is one of':
          case 'is not one of': {
            const value = {
              bool: {
                minimum_should_match: 1,
                should: filterValue.map(value => ({match_phrase: {[fieldValue]: value}}))
              }
            }
            operatorValue === 'is one of' ? filter.bool.filter.push(value) : filter.bool.must_not.push(value);
            break;
          }

          case 'exists':
          case 'does not exist': {
            const value = {exists: {field: fieldValue}}
            operatorValue === 'exists' ? filter.bool.filter.push(value) : filter.bool.must_not.push(value);
            break;
          }
        }
      }
    })

    return filter;
  }

  function getTimeRange() {
    function getRange(startTime, endTime) {
      return {
        range: {
          Time: {
            format: "strict_date_optional_time",
            gte: startTime.format('YYYY-MM-DDTHH:mm:ss.SSSZ'),
            lte: endTime.format('YYYY-MM-DDTHH:mm:ss.SSSZ')
          }
        }
      }
    }
    
    const endTime = moment();

    switch(selectedTime) {
      case 'Today': {
        const startTime = moment().utcOffset(0);
        startTime.set({hour:0,minute:0,second:0,millisecond:0})
        
        return getRange(startTime, endTime);
      }

      case 'This week': {
        const startTime = moment().startOf('isoweek' as unitOfTime.StartOf);
        return getRange(startTime, endTime);
      }

      case 'This mouth': {
        const startTime = moment().startOf('month');
        return getRange(startTime, endTime);
      }

      case 'Last 1 hour': {
        const startTime = moment().subtract(1, 'hours');
        return getRange(startTime, endTime);
      }

      case 'Last 12 hour': {
        const startTime = moment().subtract(12, 'hours');
        return getRange(startTime, endTime);
      }
    
      case 'Last 24 hour': {
        const startTime = moment().subtract(24, 'hours');
        return getRange(startTime, endTime);
      }

      case 'Absolute': {
        const { startTime, endTime } = timeRangeRef.current as {startTime: moment.Moment; endTime: moment.Moment;};
  
        return getRange(startTime, endTime);
      }
    }
  }

  function handleSearch() {
    if (isHomePage) {
      history.push({ pathname: '/dataset', search: `?filterList=${JSON.stringify(filters)}&inputValue=${inputValue}&date=${selectedTime}&timeRange=${JSON.stringify(timeRangeRef.current)}`})
      return;
    }

    const filter = getFilterData();
    const timeRange = getTimeRange();
    filter.bool.filter.push(timeRange)

    handleFilter && handleFilter({
      _filter: filter,
      _input: inputValue
    });
  }

  function extractInputValue() {
    const startIndex = inputValue.indexOf('*');
    const endIndex = inputValue.lastIndexOf('*');

    return (inputValue.substring(startIndex + 1, endIndex));
  }

  return(
    <div className={style['filter-wrapper']}  style={wrapperStyle}>
      <Row>
        <Col span={3} className={`header-form-wrapper-${isHomePage ? 'large' : 'default'}`} onMouseDown={handleDropDownClick} onClick={e => e.stopPropagation()}>
          <Select
            style={{width: '100%'}}
            value={selectedTime}
            open={isOpen}
            dropdownMatchSelectWidth={false}
            dropdownStyle={{background: 'transparent', boxShadow: 'none'}}
            dropdownRender={getDropdownRender}
          />
        </Col>
        <Col span={19} className={`header-form-wrapper-${isHomePage ? 'large' : 'default'}`} style={{paddingLeft: 15}}>
          <Select
            showSearch
            showArrow={false}
            allowClear
            value={inputValue}
            style={{ width: '100%', textAlign: 'left'}}
            dropdownStyle={{ background: '#f5f5f5' }}
            // dropdownRender={Menu => (inputValue.length ? Menu : <></>)}
            dropdownClassName="filter-input-dropdown-wrapper"
            onSearch={value => setEditingInputValue(value)}
            onChange={value =>{
              setEditingInputValue('');
              setInputValue(value);
            }}

          >
            {
              ['assetname', 'projectname', 'tags'].map(name => {
                const value = `${name}:*${editingInputValue ? editingInputValue : inputValue ? extractInputValue() : ''}*`

                return (
                  <Select.Option value={value} key={name}>
                    {value}
                  </Select.Option>
                )
              })
            }
          </Select>
          <Icon
            type="search"
            style={{position: 'absolute', right: 10, top: isHomePage ? 15 : 10, fontSize: 20, cursor: 'pointer'}}
            onClick={handleSearch}
          />
          {/* <Input.Search placeholder="Search" style={{width: '100%', marginRight: 20, marginLeft: 10}} onSearch={handleSearch}></Input.Search> */}
        </Col>
        <Col span={2}>
          <span className={style['filter-button']} style={{ height: inputHeight, lineHeight: inputHeight }} onClick={handleFilterClick}>Add Filter</span>
          {showFilterDropdown && getFilterDropdown()}
        </Col>
      </Row>
      <div style={{textAlign: 'left'}}>
        {filters.map( (filter ,index) => getFilterTag(filter, index) )}
      </div>
    </div>
  )
}

export default withRouter(FilterSearchBar);