const getPrefixComponentName = ( type, item ) => {
  const { model, format, samplerate, name} = item;

  switch(type) {

    case 'body': 
    case 'face': {
      return `${model}_${format}`;
    }

    case 'audio': {
      return `${name}_${format}_${samplerate}`
    }
    case 'video': {
      return `${name}_${format}`;
    }
    case 'feature': {
      return `${name}`;
    }
  }
}

export default getPrefixComponentName;